<template>
  <div>
    
    <div class="title">
      苹果手机使用教程【安卓手机也类似】
    </div>

    <video width=100%  controls>
      <source src="../../../../static/moc/guide.mp4" type="video/mp4">
      
    </video>  

    <div class="title">
      推荐平台
    </div>

    <div ref="wrapper">
    <ul>
      <div v-on:click="itemClick(item)"  tag="li" class="item" v-for="(item) in platformList" :key="item.id" >
        <div class="item-img-wrapper">
          <img class="item-img" :src="item.imgUrl" alt="">
        </div>
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.subTitle}}</p> 
        </div>
        <button class="item-button">
          进入
        </button>
      </div>
    </ul>
    </div>
  </div>
</template>

<script>
//import BScroll from 'better-scroll'
export default {
  name: 'HomeRecommend',
  props: ['platformList'],
  mounted () {
    console.log(this.platformList);
    //this.scroll = new BScroll(this.$refs.wrapper)
  },
  methods:{
    itemClick(item) {
      location.href = item.url + "";
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import "~styles/mixins.styl"
  .title
    line-height .8rem
    background #eee
    //margin-top .2rem
    text-indent .2rem
  .item
    margin-left 0.3rem
    margin-right 0.3rem
    border-bottom 1px solid #E6E6E6
    display flex
    height 1.3rem
    overflow hidden
    
    .item-img
      width 1.1rem
      height 1.1rem
      
      // padding .1rem
      border-radius 0.55rem
      margin 0.1rem
      // margin-left 0.2rem
      
  .item-info
    min-width 0
    flex 1
    padding .1rem
    .item-title
      margin-top 0.05rem
      line-height .54rem
      font-size .32rem
      ellipsis()
    .item-desc
      line-height .54rem
      color #ccc
      ellipsis()
  .item-button
      float right
      background #7EC0EE
      line-height 0.7rem
      height 0.7rem
      color #ffffff
      padding 0 .4rem
      border-radius .06rem
      margin-top 0.3rem
      


      //margin-top .2rem
</style>



