<template>
  <div>
    <recommend :platformList="platformList" v-if="isWechat == false"></recommend>
    <guide v-if="isWechat == true"></guide>
    </div>
</template>

<script>
import ApiUrl from '@/config/api_url'
import axios from 'axios'
import Recommend from './components/Recommend'
import guide from '../guide/components/guide' 
import { mapState } from 'vuex'

export default {
  name: 'RecommendPage',
  props: ['belong'],  //chenfanfang 
  components: {Recommend, guide},
  data () {
    return {
      isIos : false,
      isAndroid : false,
      isWechat : true,
      platformList: {}
    }
  },
  activated () {
    // 当页面加载时，进行判断，城市发生了改变
    if (this.LastCity !== this.city) {
      this.getHomeInfo()
      this.LastCity = this.city
    }
  },
  methods: {
    getHomeInfo () {
      axios.get(ApiUrl.api + 'data.json')
        .then(this.handleIndex)
    },
    handleIndex (res) {
      res = res.data;
      if (res.ret && res.data) {

        if(this.belong === 'chenfanfang' || this.belong == null) {
           
           if(this.android) {
             this.platformList = res.data.chenfanfang.android;
           }
           else {
             this.platformList = res.data.chenfanfang.ios;
           }
        }
      }
    }
  },
  mounted () {
    const u = navigator.userAgent;
    this.isWechat = u.indexOf('MicroMessenger') > -1;
    //测试数据
    // this.isWechat = true;
    this.isIos = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); // ios终端
    this.isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; // android终端

    this.getHomeInfo();
    this.LastCity = this.city;

    document.title = this.$route.meta.title;
  },
  activated() {
    document.title = this.$route.meta.title;
  }
}
</script>

<style>

</style>



