<template>
    <div>
        <div class="content">
            <div class="picture-image-message" v-if="showMessage1">
                <img class="header-icon"  src="../../../../static/moc/header_icon.jpg" alt="">
                <img class="gif"  src="../../../../static/moc/biaoqing.gif" alt="">
            </div>

            <div class="messageItem" v-if="showMessage2">
                <img class="header-icon"  src="../../../../static/moc/header_icon.jpg" alt="">
                <span class="arrow"></span>
                <div class="text-message">  
                    <img class="arrow" src="../../../../static/moc/arrow.png" alt="">
                    <span>最近超火，3千万人在这里每天领福利！</span>
                </div>
                
            </div>

            <div class="messageItem" v-if="showMessage3">
                <img class="header-icon"  src="../../../../static/moc/header_icon.jpg" alt="">
                <span class="arrow"></span>
                <div class="text-message">  
                    <img class="arrow" src="../../../../static/moc/arrow.png" alt="">
                    <span>领取新人福利</span>
                </div>
                
            </div>
        </div>
         <div class="button" v-bind:style="{bottom: buttonBottom + 'rem' }" v-on:click="btnClick" v-if="isWechat == true">
                点击加入
        </div>
        <div class="guide-hover" v-show="showHover" v-on:click="funcHideHover">
            <img v-if="isIos" class="guide-img" src="../../../../static/moc/ios_guide.png" alt="">
            <img v-if="isAndroid" class="guide-img" src="../../../../static/moc/android_guide.png" alt="">
            
        </div>
    </div>

    
</template>

<script>


export default {
  name: 'guide',
  props : {
      belong : String  //chenfanfang
  },
  data() {
    return {
      isWechat : false,
      isIos : false,
      isAndroid : true,
      showMessage1 : false,
      showMessage2 : false,
      showMessage3 : false,
      showHover : false,
      buttonBottom : 0
    };
  },
  methods : {
      funcShowMessage1() {
          this.showMessage1 = true;
      },
      funcShowMessage2() {
          this.showMessage2 = true;
      },
      funcShowMessage3() {
          this.showMessage3 = true;
      },
      btnClick() {
          this.showHover = true;
      },
      funcHideHover() {
          this.showHover = false;
      },

      setup() {
                const u = navigator.userAgent;
      this.isWechat = u.indexOf('MicroMessenger') > -1;

    //   //测试数据
    //   this.isWechat = true;
      if(this.isWechat == false) {
          if(this.belong == null || this.belong == 'chenfanfang') {
              this.$router.replace({path:'/'});
          }
          
          return;
      }

      this.isIos = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); // ios终端
      this.isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; // android终端
      this.isIos = true;
      if(this.isIos) {
          // iPhone X、iPhone XS
         var isIPhoneX =  window.devicePixelRatio && window.devicePixelRatio === 3 && window.screen.width === 375 && window.screen.height === 812;
          // iPhone XS Max
         var isIPhoneXSMax =  window.devicePixelRatio && window.devicePixelRatio === 3 && window.screen.width === 414 && window.screen.height === 896;
          // iPhone XR
         var isIPhoneXR = window.devicePixelRatio && window.devicePixelRatio === 2 && window.screen.width === 414 && window.screen.height === 896;
         if(isIPhoneX == true || isIPhoneXSMax == true || isIPhoneXR == true) {
             this.buttonBottom = 0.34;
             console.log("is iphonex ");
         }
         else {
             console.log('不是iphonex');
         }
      }

      this.showMessage1 = false;
      this.showMessage2 = false;
      this.showMessage3 = false;
      
      setTimeout(this.funcShowMessage1,1000);
      setTimeout(this.funcShowMessage2,3000);
      setTimeout(this.funcShowMessage3,4500);
      }
  },
  mounted() {
      this.setup();
  },
  activated() {
      console.log('activated');
      this.setup();
  }
  
}
</script>

<style lang="stylus" scoped >
    .guide-hover
        z-index 999
        position fixed
        top 0
        left 0
        bottom 0
        right 0
        background rgba(0, 0, 0, 0.7)
        .guide-img
            width 4rem
            right 0.2rem
            top  0.2rem
            position absolute            
    .button
        position fixed
        height 1rem
        width 100%
        bottom 0rem
        background-color #EF502B
        line-height 1rem
        text-align center
        color white
        font-size 15px
    .iphonex
        bottom  0.34rem !important
        background-color blue !important
    .content
       position  absolute
       background-color #f5f5f5 
       width  100%
       height 100%
       top 0
       bottom 0
       right 0
       left 0
       .picture-image-message
            margin-top 0.5rem
            margin-left 0.2rem
            // display inline-block
            position relative
            width 100%
            .header-icon
                position absolute
                top 0rem
                height 1rem
                width 1rem
                border-radius 10%
            .gif
                height 2rem
                margin-left 1rem
       .messageItem
            margin-top 0.5rem
            margin-left 0.2rem
            // display inline-block
            position relative
            width 100%
            color black 
            height 1rem
            .header-icon
                position absolute
                top 0rem
                height 1rem
                width 1rem
                border-radius 10%
            .gif
                height 2rem
                margin-left 1rem
            .text-message
                position absolute
                top 0rem
                bottom 0rem
                left 1rem
                right 0rem
                display inline-block
                line-height  1rem
                height 1rem
                span 
                    background-color white
                    display inline-block
                    padding-left 0.2rem
                    padding-right 0.2rem
                    border-radius 0.15rem
                    margin-left -0.15rem
                .arrow
                    margin-left 0.1rem
                    width 0.3rem
                    height 0.3rem
                       

          
</style>



