<template>
    <div>
        <guide :belong="belong" ></guide>
    </div>
</template>

<script>

import guide from './components/guide' 

export default {
  name: 'WechatGuide',
  components: {guide},
  mounted() {
    document.title = this.$route.meta.title
  },
  activated() {
    document.title = this.$route.meta.title
  },
  data () {
    return {
      belong : "chenfanfang"
    }
  }
}
</script>

<style lang="stylus" scoped >
       
          
